// خدمة إدارة الأرباح مع الحماية بالرقم السري
import { SecurityService } from './securityService';
import { reportsService } from './reportsService';
import { userDataService } from '../integrations/firebase/services';
// إزالة الاعتماد المباشر على MasterPinService هنا للحفاظ على واجهة متزامنة

export interface ProfitData {
  dailyProfit: number;
  monthlyProfit: number;
  // تقارير أرباح السحب والتحويل
  dailyWithdrawProfit: number;
  monthlyWithdrawProfit: number;
  dailyTransferProfit: number;
  monthlyTransferProfit: number;
  lastUpdated: Date;
}

export class ProfitService {
  private static readonly PROFIT_PIN_KEY_BASE = 'profit_pin';
  private static readonly MASTER_PIN_KEY_BASE = 'master_pin';

  private static getProfitPinKey(userId?: string): string {
    return userId ? `${this.PROFIT_PIN_KEY_BASE}_${userId}` : this.PROFIT_PIN_KEY_BASE;
  }

  private static getMasterPinKey(userId?: string): string {
    return userId ? `${this.MASTER_PIN_KEY_BASE}_${userId}` : this.MASTER_PIN_KEY_BASE;
  }

  // تعيين رقم سري خاص بعرض الأرباح (لم يعد مستخدماً - يستخدم الرقم السري الرئيسي)
  static setProfitPin(pin: string, userId?: string): void {
    // لا نحتاج لحفظ رقم سري منفصل - نستخدم الرقم السري الرئيسي
    console.warn('setProfitPin is deprecated. Using master PIN instead.');
  }

  // التحقق من وجود رقم سري للأرباح (يتحقق من الرقم السري الرئيسي المخزن محلياً)
  static hasProfitPin(userId?: string): boolean {
    try {
      const pin = localStorage.getItem(this.getMasterPinKey(userId));
      return !!pin && pin.length > 0;
    } catch {
      return false;
    }
  }

  // التحقق من صحة رقم سري الأرباح (يستخدم الرقم السري الرئيسي المخزن محلياً)
  static verifyProfitPin(pin: string, userId?: string): boolean {
    try {
      const encoded = localStorage.getItem(this.getMasterPinKey(userId));
      if (!encoded) return false;
      const saved = atob(encoded);
      return saved === pin;
    } catch {
      return false;
    }
  }

  // الحصول على بيانات الأرباح (محمية بالرقم السري)
  static getProfitData(userId?: string): ProfitData {
    // التأكد من تشغيل التصفير التلقائي أولاً
    reportsService.autoResetReportsIfNeeded(userId);
    
    const profits = reportsService.getProfits(userId);
    const withdrawTransferProfits = this.getWithdrawTransferProfits(userId);
    
    return {
      dailyProfit: profits.dailyProfit,
      monthlyProfit: profits.monthlyProfit,
      dailyWithdrawProfit: withdrawTransferProfits.dailyWithdrawProfit,
      monthlyWithdrawProfit: withdrawTransferProfits.monthlyWithdrawProfit,
      dailyTransferProfit: withdrawTransferProfits.dailyTransferProfit,
      monthlyTransferProfit: withdrawTransferProfits.monthlyTransferProfit,
      lastUpdated: new Date()
    };
  }

  // إضافة ربح جديد (يتم استدعاؤها عند إتمام معاملة)
  static addProfit(amount: number, userId?: string): void {
    // إضافة الربح لكل من اليومي والشهري
    reportsService.addProfit(amount, 'daily', userId);
    reportsService.addProfit(amount, 'monthly', userId);
  }

  // حساب الربح من المعاملة
  static calculateProfitFromTransaction(transaction: any): number {
    return reportsService.calculateTransactionProfit(transaction);
  }

  // تصفير الأرباح يدوياً (محمي بالرقم السري)
  static resetProfits(type: 'daily' | 'monthly', pin: string, userId?: string): boolean {
    if (!this.verifyProfitPin(pin, userId)) {
      return false;
    }

    const reportsKey = userId ? `reportsData_${userId}` : 'reportsData';
    const reportsData = reportsService.getReportsData(userId);
    
    if (type === 'daily') {
      reportsData.dailyProfit = 0;
      reportsData.lastDailyProfitResetDate = new Date();
    } else {
      reportsData.monthlyProfit = 0;
      reportsData.lastMonthlyProfitResetDate = new Date();
    }
    
    localStorage.setItem(reportsKey, JSON.stringify(reportsData));
    // مزامنة مع Firebase
    if (userId) {
      try {
        userDataService.updateUserData(userId, { reportsData: { ...reportsData } });
      } catch (e) {
        console.warn('تعذر مزامنة تصفير الأرباح مع Firebase:', e);
      }
    }
    return true;
  }

  // إزالة رقم سري الأرباح (للاختبار)
  static clearProfitPin(userId?: string): void {
    localStorage.removeItem(this.getProfitPinKey(userId));
  }

  // تحديث الأرباح من المعاملات الموجودة
  static updateProfitsFromTransactions(transactions: any[], userId?: string): void {
    // تصفير الأرباح الحالية
    const reportsKey = userId ? `reportsData_${userId}` : 'reportsData';
    const reportsData = reportsService.getReportsData(userId);
    
    let dailyProfit = 0;
    let monthlyProfit = 0;
    
    const today = new Date();
    const currentMonth = today.getMonth();
    const currentYear = today.getFullYear();
    
    // حساب الأرباح من المعاملات المرئية
    const visibleDailyTransactions = reportsService.filterVisibleTransactions(transactions, 'daily', userId);
    const visibleMonthlyTransactions = reportsService.filterVisibleTransactions(transactions, 'monthly', userId);
    
    // حساب الأرباح اليومية
    visibleDailyTransactions.forEach(transaction => {
      const tRaw0 = String(transaction.type || transaction.txnType || '').toLowerCase();
      const title0 = String(transaction.title || '');
      if (tRaw0 === 'cash_addition' || title0.includes('إيصال إضافة نقدية')) return;
      const txDate = new Date(transaction.createdAt);
      const status = String(transaction.status || 'confirmed').toLowerCase();
      if (txDate.toDateString() !== today.toDateString()) return;

      const rawType = String(transaction.type || transaction.txnType || '').toLowerCase();
      const normalizedType = rawType === 'withdrawal' ? 'withdraw' : rawType;
      const txForCalc = {
        type: normalizedType,
        amount: Number(transaction.amount || 0),
        commission: transaction.commission,
      };
      dailyProfit += this.calculateProfitFromTransaction(txForCalc);
    });
    
    // حساب الأرباح الشهرية
    visibleMonthlyTransactions.forEach(transaction => {
      const tRaw0 = String(transaction.type || transaction.txnType || '').toLowerCase();
      const title0 = String(transaction.title || '');
      if (tRaw0 === 'cash_addition' || title0.includes('إيصال إضافة نقدية')) return;
      const txDate = new Date(transaction.createdAt);
      const status = String(transaction.status || 'confirmed').toLowerCase();
      if (txDate.getMonth() !== currentMonth || txDate.getFullYear() !== currentYear) return;

      const rawType = String(transaction.type || transaction.txnType || '').toLowerCase();
      const normalizedType = rawType === 'withdrawal' ? 'withdraw' : rawType;
      const txForCalc = {
        type: normalizedType,
        amount: Number(transaction.amount || 0),
        commission: transaction.commission,
      };
      monthlyProfit += this.calculateProfitFromTransaction(txForCalc);
    });
    
    // تحديث البيانات
    reportsData.dailyProfit = Math.round(dailyProfit * 100) / 100;
    reportsData.monthlyProfit = Math.round(monthlyProfit * 100) / 100;
    
    localStorage.setItem(reportsKey, JSON.stringify(reportsData));
    // مزامنة مع Firebase
    if (userId) {
      try {
        userDataService.updateUserData(userId, { reportsData: { ...reportsData } });
      } catch (e) {
        console.warn('تعذر مزامنة تحديث الأرباح من المعاملات مع Firebase:', e);
      }
    }
  }

  // حساب أرباح السحب والتحويل منفصلة
  static getWithdrawTransferProfits(userId?: string): {
    dailyWithdrawProfit: number;
    monthlyWithdrawProfit: number;
    dailyTransferProfit: number;
    monthlyTransferProfit: number;
  } {
    const reportsData = reportsService.getReportsData(userId);
    const today = new Date();
    const currentMonth = today.getMonth();
    const currentYear = today.getFullYear();

    // الحصول على المعاملات من localStorage
    // توحيد صيغة المفتاح لتتطابق مع UserDashboardPage: transactions_${userId || 'default'}
    const transactionsKey = `transactions_${userId || 'default'}`;
    const transactions = JSON.parse(localStorage.getItem(transactionsKey) || '[]');

    let dailyWithdrawProfit = 0;
    let monthlyWithdrawProfit = 0;
    let dailyTransferProfit = 0;
    let monthlyTransferProfit = 0;

    // تصفية المعاملات المرئية
    const visibleDailyTransactions = reportsService.filterVisibleTransactions(transactions, 'daily', userId);
    const visibleMonthlyTransactions = reportsService.filterVisibleTransactions(transactions, 'monthly', userId);

    // حساب أرباح السحب اليومية
    visibleDailyTransactions.forEach(transaction => {
      const tRaw0 = String(transaction.type || transaction.txnType || '').toLowerCase();
      const title0 = String(transaction.title || '');
      if (tRaw0 === 'cash_addition' || title0.includes('إيصال إضافة نقدية')) return;
      const txDate = new Date(transaction.createdAt);
      const status = String(transaction.status || 'confirmed').toLowerCase();
      if (txDate.toDateString() !== today.toDateString()) return;

      const rawType = String(transaction.type || transaction.txnType || '').toLowerCase();
      const normalizedType = rawType === 'withdrawal' ? 'withdraw' : rawType;
      const txForCalc = {
        type: normalizedType,
        amount: Number(transaction.amount || 0),
        commission: transaction.commission,
      };

      if (txForCalc.type === 'withdraw') {
        dailyWithdrawProfit += this.calculateProfitFromTransaction(txForCalc);
      } else if (txForCalc.type === 'receive' || txForCalc.type === 'send' || txForCalc.type === 'transfer') {
        dailyTransferProfit += this.calculateProfitFromTransaction(txForCalc);
      }
    });

    // حساب أرباح السحب الشهرية
    visibleMonthlyTransactions.forEach(transaction => {
      const tRaw0 = String(transaction.type || transaction.txnType || '').toLowerCase();
      const title0 = String(transaction.title || '');
      if (tRaw0 === 'cash_addition' || title0.includes('إيصال إضافة نقدية')) return;
      const txDate = new Date(transaction.createdAt);
      const status = String(transaction.status || 'confirmed').toLowerCase();
      if (txDate.getMonth() !== currentMonth || txDate.getFullYear() !== currentYear) return;

      const rawType = String(transaction.type || transaction.txnType || '').toLowerCase();
      const normalizedType = rawType === 'withdrawal' ? 'withdraw' : rawType;
      const txForCalc = {
        type: normalizedType,
        amount: Number(transaction.amount || 0),
        commission: transaction.commission,
      };

      if (txForCalc.type === 'withdraw') {
        monthlyWithdrawProfit += this.calculateProfitFromTransaction(txForCalc);
      } else if (txForCalc.type === 'receive' || txForCalc.type === 'send' || txForCalc.type === 'transfer') {
        monthlyTransferProfit += this.calculateProfitFromTransaction(txForCalc);
      }
    });

    return {
      dailyWithdrawProfit: Math.round(dailyWithdrawProfit * 100) / 100,
      monthlyWithdrawProfit: Math.round(monthlyWithdrawProfit * 100) / 100,
      dailyTransferProfit: Math.round(dailyTransferProfit * 100) / 100,
      monthlyTransferProfit: Math.round(monthlyTransferProfit * 100) / 100
    };
  }
}
